@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .links{
        @apply  hover:text-zinc-400 hover:cursor-pointer transition-colors duration-300
    }
    .pageMargin{
        @apply my-12 md:my-24 lg:my-32 flex-grow
    }
    .text-colored{
        @apply bg-gradient-to-r from-blue-600 to-purple-600 dark:from-red-600 dark:to-yellow-500 bg-clip-text text-transparent
    }
    .typed-cursor {
        opacity: 1;
        animation: blink .7s infinite;
    }
    text-colored {
        @apply bg-gradient-to-r from-blue-600 to-purple-600 dark:from-red-600 dark:to-yellow-500 bg-clip-text text-transparent
    }
    .items-grid{
        @apply flex flex-wrap gap-4 text-center text-sm my-4 justify-center md:justify-start
    }
    .resumeSection{
        @apply flex flex-col text-center md:text-start text-2xl md:text-3xl font-semibold mb-4
    }
    .text-light{
        @apply text-zinc-600 dark:text-zinc-400 border-zinc-600 dark:border-zinc-400
    }
    .project{
        @apply rounded-xl bg-zinc-200 dark:bg-zinc-800 w-64 text-base overflow-hidden shadow-lg h-fit
    }
}